:host {
  display: flex;
  height: 100%;
  width: 100%;
}
.async-img-container {
  display: flex;
}

.async-img-container.skeleton {
  border-radius: 4px;
  background: linear-gradient(
    90deg,
    rgba(248, 248, 248, 0.144),
    rgba(239, 239, 239, 0.356),
    rgba(231, 231, 231, 0.147)
  );
  background-size: 200%;
  animation: skeleton 1s infinite reverse;
}

@keyframes skeleton {
  0% {
    background-position: -100% 0;
  }
  100% {
    background-position: 100%;
  }
}
